/* Elements */
const generateBtn = document.querySelector('.generate');
const numOfParagraphs = document.querySelector('.paragraphs');
const loremIpsumText = document.querySelector('.lorem-ipsum');
const copyBtn = document.querySelector('.copy');

/* /////////////// Lorem Ipsum Generator /////////// */

const LoremIpsum = require("lorem-ipsum").LoremIpsum;

const lorem = new LoremIpsum({
    sentencesPerParagraph: {
        max: 8,
        min: 4
    },
    wordsPerSentence: {
        max: 16,
        min: 4
    }
});

/* Function genetate Lorem Ipsum text */

function generateLoremIpsum() {
    const number = parseInt(numOfParagraphs.value);
    const text = lorem.generateParagraphs(number);

    loremIpsumText.innerText = text;
}

/* Copy Lorem Ipsum text */

function copyToClipboard() {
    const el = document.createElement('textarea');
    el.value = loremIpsumText.textContent;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
}

/* Listeners */

generateBtn.addEventListener('click', event => {
    event.preventDefault();

    generateLoremIpsum();
});

copyBtn.addEventListener('click', event => {
    event.preventDefault();

    copyToClipboard();
})